@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,700;1,300,500&display=swap);
* {
  padding: 0px;
  margin: 0px;
  font-family: "Poppins", sans-serif;
}
a {
  text-decoration: none;
}
body {
  background-color: radial-gradient(circle farthest-side, #1e1e1e, #292929);
  background: radial-gradient(circle farthest-side, #1e1e1e, #292929);
  /*scrollbar for firefox*/
  overflow-y: scroll;
  scrollbar-color: #00ccff black;
  scrollbar-width: auto;
}

/*chrome and safari*/
body::-webkit-scrollbar {
  width: 1em;
  background-color: #000000;
}

body::-webkit-scrollbar-track {
  /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
  width: 1em;
  background: rgb(0, 0, 0);
  background-clip: content-box;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #00ccff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right bottom,
    /* #7f7fd5,
    #00ccff,
    #56cc9d */
    #363636,
    #2f3030,
    #2e2e2e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/*chrome and safari*/
.MuiDrawer-paper::-webkit-scrollbar {
  width: 0.5em;
  background-color: #000000;
}

.MuiDrawer-paper::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: rgb(0, 0, 0);
  background-clip: content-box;
}

.MuiDrawer-paper::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #b3d4fc;
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    color-stop(0.24, #00ccff),
    color-stop(0.78, #b3d4fc),
    color-stop(0.86, #00ccff)
  );
}

/*for firefox*/
.MuiDrawer-paper {
  overflow-y: scroll;
  scrollbar-color: #00ccff black;
  scrollbar-width: thin;
}

.side-list-item {
  text-decoration: none;
}
.main_content_cards {
  display: grid;
  margin: 2vh 1vh;
  grid-template-columns: 1fr 4fr;
  grid-gap: 20px;
